.work-item-header {
  @extend %firstSectionPadding;
  padding-bottom: 220px;
  @include lg {
    padding-bottom: 108px; }
  @include sm {
    padding-bottom: 50px; }
  &__text {
    max-width: 510px; }
  &__title {
 } }    // margin-bottom: 10px

.work-item-hero {
  display: flex;
  align-items: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 0;
  min-height: 1024px;
  // height: 71.11vw
  // max-height: 100vh
  color: $white;
  @include lg {
    min-height: 820px; }
  @include sm {
    min-height: 230px; }
  &__inner {
    padding-top: 20vw;
    padding-bottom: 20vw;
    @include lg {
      padding-top: 20vw;
      padding-bottom: 20vw; }
    @include sm {
      padding-top: 20vw;
      padding-bottom: 20vw; } }
  &__title {
    color: $white;
    // margin-bottom: 10px
    // +lg
    //   margin-bottom: 20px
    // +sm
 }    //   margin-bottom: 4px
  &__text {
    max-width: 510px;
    // padding-top: 100px
 } }    // padding-bottom: 100px

.work-item-descr {
  // &__title
  //// margin-bottom: 7px
  //// +sm
  ////   margin-bottom: 0
  &__top {
    margin-bottom: 203px;
    @include lg {
      margin-bottom: 100px; }
    @include sm {
      margin-bottom: 46px; } }
  &__col {
    &:not(:last-child) {
      @include sm {
        margin-bottom: 46px; } }
    p {
      max-width: 450px; } } }

.images-section {
  background-color: $imagesSectionBg;
  &_light {
    background-color: rgba(152, 149, 162, 0.1); }
  &__image {
    padding-top: 140px;
    @include lg {
      padding-top: 120px; }
    @include sm {
      padding-top: 30px; }
    &:last-child {
      padding-bottom: 140px;
      @include lg {
        padding-bottom: 120px; }
      @include sm {
        padding-bottom: 30px; } }
    img {
      max-width: 100%;
      display: block; } } }

.text-section {
  padding: 202px 0 105px;
  @include lg {
    padding: 110px 0 49px; }
  @include sm {
    padding: 50px 0 22px;
    text-align: left; }
  // &__title
  //   margin-bottom: 10px
  //   +lg
  //     margin-bottom: 6px
  //   +sm
 }  //     margin-bottom: 0

.blockquote-section {
  padding: 205px 0;
  @include lg {
    padding: 112px 0; }
  @include sm {
    padding: 52px 0; }
  &__inner {
    max-width: 920px;
    width: 100%;
    margin: 0 auto;
    padding: 0 30px; } }
